/* Dropdown Button */
.dropdown-btn {
  display: flex;
  align-items: center;
  padding: 10px 15px;
  background: none; /* No background color */
  color: #232f3e; /* Text color */
  border: none;
  font-size: 0.9rem;
  cursor: pointer;
  border-radius: 5px;
  position: relative; /* For positioning the arrow */
  transition: color 0.3s ease; /* Smooth transition for color change */
}

.dropdown-btn:hover {
  color: #1b2a36; /* Darker color on hover */
}

/* Dropdown Arrow */
.dropdown-btn::after {
  content: '';
  border: 5px solid transparent;
  border-top-color: #232f3e; /* Arrow color */
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none; /* So the arrow doesn't block clicks */
}

/* Dropdown Menu */
.dropdown-menu {
  display: none; /* Hidden by default */
  position: absolute;
  top: 100%; /* Align with the bottom of the button */
  border: 1px solid #ddd;
  border-radius: 4px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow effect */
  width: 200px; /* Adjust width as needed */
  z-index: 2000;
  background-color: white;
  max-height: 200px; /* Set a maximum height */
  overflow-y: auto; /* Enable vertical scrolling */

  /* Custom Scrollbar */
  scrollbar-width: thin; /* For Firefox */
  scrollbar-color: #4A90E2 #f0f0f0; /* Scroll thumb and track colors */
}


.dropdown-menu::-webkit-scrollbar-thumb {
  background-color: #4A90E2; /* Scroll thumb color */
  border-radius: 10px; /* Rounded corners for the scroll thumb */
  border: 2px solid #f0f0f0; /* Creates space around the thumb */
}

/* Show Dropdown Menu */
.dropdown-menu.show {
  display: block;
}

/* Dropdown Items */
.dropdown-menu a {
  text-decoration: none;
  color: #232f3e;
  padding: 10px 15px;
  display: block;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.dropdown-menu a:hover {
  background-color: #f5f5f5; /* Light background on hover */
}


