  margin: 50px 0px;
}

/* Title Styling */
.explore-title {
  text-align: center;
  font-size: 1.6rem;
  color: var(--primary-color);
  font-weight: 600;
  margin-bottom: 30px;
  margin-top: 10px;
}

/* Grid Layout for PDFs */
.pdf-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
}

/* Individual PDF Card */
.pdf-card {
  background-color: var(--card-background);
  padding: 25px;
  border-radius: var(--border-radius);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform var(--transition-speed) ease, box-shadow var(--transition-speed) ease;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.pdf-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
}

/* PDF Title */
.pdf-title {
  font-size: 1.8rem;
  color: var(--primary-color);
  margin-bottom: 15px;
  font-weight: 500;
}

/* PDF Description */
.pdf-description {
  font-size: 1.1rem;
  color: var(--light-text-color);
  margin-bottom: 25px;
  line-height: 1.6;
}

/* Download Button */
.pdf-download-button {
  padding: 12px 30px;
  background-color: var(--secondary-color);
  color: #fff;
  font-size: 1rem;
  border: none;
  border-radius: 25px;
  text-decoration: none;
  cursor: pointer;
  transition: background-color var(--transition-speed) ease, transform var(--transition-speed) ease;
  font-weight: 500;
  display: inline-block;
}

.pdf-download-button:hover,
.pdf-download-button:focus {
  background-color: var(--button-hover-color);
  transform: scale(1.05);
  outline: none; /* Remove default outline */
  /* Optional: Add a custom focus outline for accessibility */
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.5);
}

/* Spinner Container */
.explore-spinner-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80vh; /* Full viewport height */
}

/* Error Container */
.explore-error-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80vh;
  color: red;
  font-size: 1.5rem;
  text-align: center;
  padding: 0 20px;
}


/* Load More Button Container */
.explore-load-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 30px 0px;
}

/* Load More Button */
.explore-load-more-button {
  padding: 12px 30px;
  background-color: var(--secondary-color, #007bff); /* Fallback color if variable not defined */
  color: #fff;
  font-size: 0.9rem;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  font-weight: 500;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.explore-load-more-button:hover,
.explore-load-more-button:focus {
  background-color: #0056b3; /* Darker blue on hover */
  transform: scale(1.05); /* Slightly enlarge on hover */
  outline: none; /* Remove default outline */
  /* Optional: Add a custom focus outline for accessibility */
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.5);
}

.explore-load-more-button:disabled {
  background-color: #6c757d; /* Gray color when disabled */
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}


