/* ipad css */
@media (max-width:786px) {
   
  .nav-link-main {
    display: none;
    flex-direction: column;
    gap: 10px;
  }


  .hamburger {
    display: block;
  }

  .nav-link-main.open {
    display: flex;
    align-items: center;
    width: 100vw;
    margin: 0;
    padding: 15px; 
  }
 

  .nav-link-main.open .nav-link,
